import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Box, VStack, Heading, Text, Divider, Button, Select, Input, Textarea, FormControl, FormLabel, Alert, AlertIcon, HStack, Spinner
} from '@chakra-ui/react';

const CourseMaterials = () => {
  const [role, setRole] = useState(''); // Track user role (student/teacher)
  const [courseMaterials, setCourseMaterials] = useState([]); // List of course materials
  const [materialFile, setMaterialFile] = useState(null); // Course material file to upload
  const [success, setSuccess] = useState(''); // Success message
  const [error, setError] = useState(''); // Error message
  const [loading, setLoading] = useState(false);

  // Fetch role and course materials on component mount
  useEffect(() => {
    async function fetchData() {
      try {
        const sessionResponse = await axios.get('/api/session'); // Fetch role from session
        setRole(sessionResponse.data.role);

        const materialsResponse = await axios.get('/api/course_materials'); // Fetch course materials
        setCourseMaterials(materialsResponse.data.course_materials);
      } catch (err) {
        console.error(err);
        setError('Failed to load data.');
      }
    }
    fetchData();
  }, []);

  // Handle course material upload
  const handleMaterialUpload = async (e) => {
    e.preventDefault();
    if (!materialFile) {
      setError('Please upload a course material file.');
      return;
    }

    const formData = new FormData();
    formData.append('course_material', materialFile);

    try {
      const response = await axios.post('/api/course_materials', formData);
      setSuccess('Course material uploaded successfully!');
      setError('');

      // Optionally, refresh the list of materials
      const materialsResponse = await axios.get('/api/course_materials');
      setCourseMaterials(materialsResponse.data.course_materials);
    } catch (err) {
      setError('Failed to upload course material.');
      setSuccess('');
    }
  };

  // Sort course materials by upload date in descending order
  const sortedCourseMaterials = [...courseMaterials].sort((a, b) => new Date(b.upload_date) - new Date(a.upload_date));

  return (
    <Box p={6} bg="gray.50" minHeight="100vh">
      <Heading fontSize="3xl" mb={6} color="teal.600" textAlign="center">
        Course Materials
      </Heading>

      {/* Display error or success messages */}
      {error && (
        <Alert status="error" mb={4} borderRadius="md" boxShadow="md">
          <AlertIcon />
          {error}
        </Alert>
      )}
      {success && (
        <Alert status="success" mb={4} borderRadius="md" boxShadow="md">
          <AlertIcon />
          {success}
        </Alert>
      )}

      {/* Course Material Upload Form for Teachers */}
      {role === 'teacher' && (
        <Box
          p={6}
          bg="white"
          borderRadius="md"
          boxShadow="lg"
          mb={8}
          border="1px solid"
          borderColor="gray.200"
          maxWidth="600px"
          mx="auto"
        >
          <form onSubmit={handleMaterialUpload} encType="multipart/form-data">
            <FormControl id="material" isRequired>
              <FormLabel>Upload Course Material:</FormLabel>
              <Input
                type="file"
                onChange={(e) => setMaterialFile(e.target.files[0])}
                focusBorderColor="teal.400"
                bg="gray.50"
              />
            </FormControl>

            <Button type="submit" colorScheme="teal" mt={4} width="full" _hover={{ bg: "teal.500" }}>
              Upload
            </Button>
          </form>
        </Box>
      )}

      {/* List of Available Course Materials for Download */}
      <Box mt={8} maxWidth="800px" mx="auto">
        <Heading size="md" mb={6} color="teal.600" textAlign="center">
          Available Course Materials:
        </Heading>
        <VStack spacing={6} align="stretch">
          {sortedCourseMaterials.length > 0 ? (
            sortedCourseMaterials.map((material) => (
              <Box
                key={material.id}
                p={6}
                borderRadius="md"
                bg="gray.50"
                border="1px solid"
                borderColor="gray.200"
                boxShadow="md"
                _hover={{ boxShadow: "lg", transform: "scale(1.02)", transition: "all 0.2s ease-in-out" }}
              >
                <HStack justify="space-between" mb={2}>
                  <Text fontWeight="bold" color="teal.600">
                    <a href={`/download/${material.file_path}`} target="_blank" rel="noopener noreferrer">
                      {material.filename}
                    </a>
                  </Text>
                  <Text fontSize="sm" color="gray.500">
                    Uploaded on: {material.upload_date}
                  </Text>
                </HStack>

                <Text fontSize="sm" color="gray.600">
                  <strong>Teacher:</strong> {material.teacher_name}
                </Text>
                <Divider my={4} />
              </Box>
            ))
          ) : (
            <Text>No course materials available.</Text>
          )}
        </VStack>
      </Box>

      {loading && (
        <Box
          position="fixed"
          top="0"
          left="0"
          width="100%"
          height="100%"
          backgroundColor="rgba(0, 0, 0, 0.5)"
          zIndex="9999"
          display="flex"
          justifyContent="center"
          alignItems="center"
          color="white"
          fontSize="18px"
          textAlign="center"
        >
          <Spinner size="xl" />
          <p>Loading...</p>
        </Box>
      )}
    </Box>
  );
};

export default CourseMaterials;
