import React, { useState } from 'react';
import { Flex, Box, Button, Input, Stack, Heading, Text, Link as ChakraLink, useToast, Icon } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { FiUser, FiLock } from 'react-icons/fi'; // Icons for username and password inputs

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const toast = useToast(); // Toast for better feedback messages

  const handleLogin = async () => {
    try {
      const response = await axios.post('/api/login', {
        username,
        password,
      });

      if (response.status === 200) {
        onLogin(response.data.username, response.data.token);

        toast({
          title: 'Login Successful!',
          description: 'Welcome back.',
          status: 'success',
          duration: 3000,
          isClosable: true,
        });

        navigate('/dashboard');
      }
    } catch (error) {
      if (error.response && error.response.status === 401) {
        setError('Invalid username or password');
      } else {
        setError('An error occurred. Please try again.');
      }
    }
  };

  return (
    <Flex align="center" justify="center" minHeight="83vh" bgGradient="linear(to-r, gray.200, gray.100)">
      <Box p={8} maxWidth="400px" borderWidth={1} borderRadius={12} boxShadow="2xl" bg="white">
        <Heading as="h2" size="xl" textAlign="center" mb={6} color="blue.700" fontWeight="bold">
          Welcome Back
        </Heading>

        {/* Show error message if login fails */}
        {error && (
          <Text color="red.500" mb={4} fontSize="sm" textAlign="center">
            {error}
          </Text>
        )}

        <Stack spacing={4}>
          {/* Username Input with Icon */}
          <Flex align="center">
            <Icon as={FiUser} color="gray.500" mr={3} />
            <Input
              placeholder="Username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              variant="filled"
              focusBorderColor="blue.400"
            />
          </Flex>

          {/* Password Input with Icon */}
          <Flex align="center">
            <Icon as={FiLock} color="gray.500" mr={3} />
            <Input
              placeholder="Password"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              variant="filled"
              focusBorderColor="blue.400"
            />
          </Flex>

          <Button
            colorScheme="blue"
            width="full"
            mt={4}
            onClick={handleLogin}
            _hover={{ bg: 'blue.600' }}
            _active={{ bg: 'blue.700' }}
          >
            Log In
          </Button>
        </Stack>

        {/* Link to Registration Page */}
        <Text mt={6} textAlign="center" color="gray.600">
          Don't have an account?{' '}
          <ChakraLink color="blue.500" onClick={() => navigate('/register')} _hover={{ textDecoration: 'underline' }}>
            Register here
          </ChakraLink>
        </Text>
      </Box>
    </Flex>
  );
}

export default Login;
