import React, { useEffect, useState } from 'react';
import {
  Box,
  VStack,
  Link,
  useDisclosure,
  IconButton,
  Collapse,
  Button,
  Flex,
  Text,
  Spinner,
  Divider,
  Tooltip,
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon } from '@chakra-ui/icons';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Sidebar = ({ onLogout, setCollapsed, collapsed = true, isLoggedIn }) => {
  const { isOpen, onToggle } = useDisclosure();
  const [role, setRole] = useState('');
  const [username, setUsername] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchData() {
      try {
        const sessionResponse = await axios.get('/api/session');
        if (sessionResponse.data.logged_in) {
          setRole(sessionResponse.data.role);
          setUsername(sessionResponse.data.username);
        } else {
          navigate('/login'); // If session invalid, redirect to login
        }
      } catch (err) {
        console.error('Failed to fetch session data:', err.response ? err.response.data : err.message);
        setError('Failed to load session data.');
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, [navigate]);

  const handleToggle = () => {
    setCollapsed(!collapsed);
  };

  const handleNavigation = (path) => {
    navigate(path);
    setCollapsed(true);
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" height="100vh" bg="gray.800">
        <Spinner color="white" size="lg" />
      </Box>
    );
  }

  return (
    <Box
      width={collapsed ? '80px' : '250px'}
      transition="width 0.3s"
      bg="gray.800"
      height="100vh"
      position="fixed"
      top="0"
      left="0"
      zIndex="2"
      boxShadow="lg"
    >
      <IconButton
        aria-label="Toggle Sidebar"
        icon={collapsed ? <HamburgerIcon /> : <CloseIcon />}
        onClick={handleToggle}
        colorScheme="teal"
        variant="ghost"
        size="lg"
        isRound
        mt={4}
        ml={4}
        _hover={{ bg: 'teal.600' }}
        transition="0.3s"
      />

      <Collapse in={!collapsed} animateOpacity>
        <VStack align="left" spacing={5} mt={10} p={6}>
          <Text color="gray.300" fontSize="sm">
            Welcome, {username}
          </Text>

          <Divider borderColor="gray.600" />

          {!isLoggedIn ? (
            <>
              <Link
                onClick={() => handleNavigation('/login')}
                color="gray.300"
                fontSize="lg"
                _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
              >
                Login
              </Link>
              <Link
                onClick={() => handleNavigation('/register')}
                color="gray.300"
                fontSize="lg"
                _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
              >
                Register
              </Link>
            </>
          ) : (
            <>
              <Link
                onClick={() => handleNavigation('/')}
                color="gray.300"
                fontSize="lg"
                _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
              >
                Dashboard
              </Link>

              {role === 'student' && (
                <>
                  <Link
                    onClick={() => handleNavigation('/assignments')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    Upload Assignments
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/feedback')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    View Feedback
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/materials')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    View Course Materials
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/queries')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    Ask a Query
                  </Link>
                </>
              )}

              {role === 'teacher' && (
                <>
                  <Link
                    onClick={() => handleNavigation('/assignments')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    View Assignments
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/feedback')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    Upload Feedback
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/materials')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    Upload Course Materials
                  </Link>
                  <Link
                    onClick={() => handleNavigation('/queries')}
                    color="gray.300"
                    fontSize="lg"
                    _hover={{ color: 'teal.300', transform: 'scale(1.05)', transition: '0.2s' }}
                  >
                    View Queries
                  </Link>
                </>
              )}

              <Divider borderColor="gray.600" />

              <Flex align="center">
                <Button
                  colorScheme="red"
                  onClick={onLogout}
                  variant="outline"
                  _hover={{ bg: 'red.600', color: 'white' }}
                  mt={4}
                  width="full"
                >
                  Logout
                </Button>
              </Flex>
            </>
          )}
        </VStack>
      </Collapse>
    </Box>
  );
};

export default Sidebar;
