import React, { useState } from 'react';
import { Flex, Box, Button, Input, Stack, Heading, Select, Text, Alert, AlertIcon } from '@chakra-ui/react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Register() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('');
  const [error, setError] = useState('');

  const navigate = useNavigate();

  const handleRegister = async () => {
    try {
      console.log("Register button clicked");
      const response = await axios.post('/api/register', {
        username,
        password,
        role,
      });
      if (response.status === 200) {
        navigate(response.data.redirect);
      }
      console.log(response.data);
    } catch (error) {
      console.error('Error during registration', error);
      setError('Registration failed. Try again.');
    }
  };

  return (
    <Flex align="center" justify="center" minHeight="100vh" bgGradient="linear(to-br, blue.100, gray.200)">
      <Box p={8} maxWidth="400px" borderWidth={2} borderRadius="lg" boxShadow="lg" bg="white">
        <Heading as="h2" size="lg" textAlign="center" mb={6} color="blue.600">
          Register
        </Heading>
        {error && (
          <Alert status="error" mb={4} borderRadius={8}>
            <AlertIcon />
            {error}
          </Alert>
        )}
        <Stack spacing={4}>
          <Input
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            variant="flushed"
            focusBorderColor="blue.400"
          />
          <Input
            placeholder="Password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            variant="flushed"
            focusBorderColor="blue.400"
          />
          <Select
            placeholder="Select Role"
            value={role}
            onChange={(e) => setRole(e.target.value)}
            variant="flushed"
            focusBorderColor="blue.400"
          >
            <option value="student">Student</option>
            <option value="teacher">Teacher</option>
          </Select>

          <Button
            colorScheme="blue"
            variant="solid"
            width="full"
            onClick={handleRegister}
            mt={4}
            borderRadius="full"
            _hover={{ bg: 'blue.600' }}
          >
            Register
          </Button>
        </Stack>
        <Text textAlign="center" mt={4}>
          Already have an account? <a href="/login" style={{ color: 'blue.600' }}>Login here</a>
        </Text>
      </Box>
    </Flex>
  );
}

export default Register;
