import React from 'react';
import { Box, Flex, Heading, Text, Icon } from '@chakra-ui/react';
import { FiUser } from 'react-icons/fi'; // Adding an icon for a modern touch

const Header = ({ isLoggedIn, username }) => {
  return (
    <Box 
      bgGradient="linear(to-r, blue.700, blue.600)" 
      p={10} 
      position="relative" 
      boxShadow="lg" // Added box shadow for depth
      borderBottomWidth={2}
      borderBottomColor="blue.900"
    >
      <Flex justify="space-between" align="center">
        {/* Center the LMS System title */}
        <Heading
          color="white"
          fontSize={{ base: '2xl', md: '3xl', lg: '4xl' }}
          fontWeight="bold"
          letterSpacing="widest"
        >
          LMS System
        </Heading>

        {/* Show greetings and username aligned to the right after logging in */}
        {isLoggedIn && (
          <Flex align="center">
            <Icon as={FiUser} color="white" boxSize={6} mr={3} />
            <Text 
              color="white" 
              fontSize="lg"
              fontWeight="medium"
              _hover={{ textDecoration: 'underline' }} // Added hover effect for style
            >
              Hello, {username}
            </Text>
          </Flex>
        )}
      </Flex>
    </Box>
  );
};

export default Header;
