import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
    Box, Button, Textarea, Radio, RadioGroup, Stack, Select, Spinner, Alert, AlertIcon, FormControl, FormLabel, Divider, VStack, Heading, Text, HStack, useToast,
} from '@chakra-ui/react';
import FeedbackFormatter from './FeedbackFormatter';

const Queries = () => {
    const [queryType, setQueryType] = useState('llm');
    const [teachers, setTeachers] = useState([]);
    const [courseMaterials, setCourseMaterials] = useState([]);
    const [queries, setQueries] = useState([]);
    const [teacherSelected, setTeacherSelected] = useState('');
    const [courseMaterialSelected, setCourseMaterialSelected] = useState('');
    const [queryText, setQueryText] = useState('');
    const [answerTexts, setAnswerTexts] = useState({});
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [role, setRole] = useState('');

    const toast = useToast();

    useEffect(() => {
        async function fetchData() {
            try {
                const sessionResponse = await axios.get('/api/session');
                setRole(sessionResponse.data.role);

                const materialsResponse = await axios.get('/api/course_materials');
                setCourseMaterials(materialsResponse.data.course_materials);

                const teachersResponse = await axios.get('/api/forum');
                setTeachers(teachersResponse.data.teachers);

                const queriesResponse = await axios.get('/api/forum');
                setQueries(queriesResponse.data.queries);
            } catch (err) {
                console.error(err);
                setError('Failed to load data.');
            }
        }
        fetchData();
    }, []);

    const handleQuerySubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        setError('');
        setSuccess('');

        const data = {
            query_type: queryType,
            query: queryText,
            teacher: queryType === 'teacher' ? teacherSelected : '',
            course_material: courseMaterialSelected
        };

        try {
            const response = await axios.post('/api/forum', data);
            if (response.status === 200) {
                setSuccess('Query submitted successfully!');
                setQueryText('');

                const queriesResponse = await axios.get('/api/forum');
                setQueries(queriesResponse.data.queries);
            } else {
                setError('Failed to submit query.');
            }
        } catch (err) {
            setError('Failed to submit query.');
        } finally {
            setLoading(false);
        }
    };

    const handleAnswerSubmit = async (queryId) => {
        setLoading(true);
        setError('');
        setSuccess('');

        const data = {
            query_id: queryId,
            answer_text: answerTexts[queryId],
        };

        try {
            const response = await axios.post('/api/forum', data);
            if (response.status === 200) {
                setSuccess('Answer submitted successfully!');

                const queriesResponse = await axios.get('/api/forum');
                setQueries(queriesResponse.data.queries);
            } else {
                setError('Failed to submit answer.');
            }
        } catch (err) {
            setError('Failed to submit answer.');
        } finally {
            setLoading(false);
        }
    };

    const handleAnswerChange = (queryId, value) => {
        setAnswerTexts({ ...answerTexts, [queryId]: value });
    };

    const sortedQueries = [...queries].sort((a, b) => new Date(b.date) - new Date(a.date));

    return (
        <Box p={8} bgGradient="linear(to-br, gray.100, white)" borderRadius={12} boxShadow="xl">
            <Heading size="lg" mb={6} color="blue.700" textAlign="center">
                Queries
            </Heading>

            {success && (
                <Alert status="success" mb={4} borderRadius={8}>
                    <AlertIcon />
                    {success}
                </Alert>
            )}

            {error && (
                <Alert status="error" mb={4} borderRadius={8}>
                    <AlertIcon />
                    {error}
                </Alert>
            )}

            {role === 'student' && (
                <form onSubmit={handleQuerySubmit}>
                    <VStack spacing={4} align="stretch">
                        <FormControl>
                            <FormLabel>Query Type</FormLabel>
                            <RadioGroup onChange={setQueryType} value={queryType}>
                                <Stack direction="row">
                                    <Radio value="llm">Ask LLM</Radio>
                                    <Radio value="teacher">Ask Teacher</Radio>
                                </Stack>
                            </RadioGroup>
                        </FormControl>

                        {queryType === 'teacher' && (
                            <FormControl>
                                <FormLabel>Select Teacher</FormLabel>
                                <Select placeholder="Select a teacher" onChange={(e) => setTeacherSelected(e.target.value)}>
                                    {teachers.map((teacher) => (
                                        <option key={teacher.username} value={teacher.username}>{teacher.username}</option>
                                    ))}
                                </Select>
                            </FormControl>
                        )}

                        <FormControl>
                            <FormLabel>Select Course Material</FormLabel>
                            <Select placeholder="Select course material" onChange={(e) => setCourseMaterialSelected(e.target.value)}>
                                {courseMaterials.map((material) => (
                                    <option key={material.file_path} value={material.file_path}>{material.filename}</option>
                                ))}
                            </Select>
                        </FormControl>

                        <FormControl>
                            <FormLabel>Your Query</FormLabel>
                            <Textarea
                                placeholder="Enter your query..."
                                value={queryText}
                                onChange={(e) => setQueryText(e.target.value)}
                                bg="gray.50"
                                _hover={{ bg: 'gray.100' }}
                                _focus={{ bg: 'white', borderColor: 'blue.500' }}
                                rows={4}
                                required
                            />
                        </FormControl>

                        <Button type="submit" colorScheme="blue" isLoading={loading} borderRadius="full">
                            Submit Query
                        </Button>
                    </VStack>
                </form>
            )}

            <Box mt={8}>
                <Heading size="md" mb={4} color="gray.700">Ongoing Queries</Heading>
                <VStack spacing={6} align="stretch">
                    {sortedQueries.length === 0 ? (
                        <Text>No ongoing queries at the moment.</Text>
                    ) : (
                        sortedQueries.map((query) => {
                            const isPending = query.answer_text === "Pending";

                            return (
                                <Box
                                    key={query.query_id}
                                    borderWidth="1px"
                                    borderRadius="lg"
                                    p={6}
                                    shadow="sm"
                                    bg={isPending ? "yellow.100" : "gray.50"}
                                    borderColor={isPending ? "yellow.400" : "gray.200"}
                                >
                                    <HStack justify="space-between">
                                        <Text fontWeight="bold" color="blue.800">{query.content}</Text>
                                        <Text fontSize="sm" color="gray.500">Submitted on: {query.date}</Text>
                                    </HStack>
                                    <Text fontSize="sm" color="gray.600">Teacher/LLM: {query.teacher_name || 'LLM'}</Text>
                                    <Text fontSize="sm" mt={2}><strong>Course Material:</strong> {query.context_file_path || 'N/A'}</Text>
                                    <Divider my={3} />
                                    <Text fontSize="sm"><strong>Answer:</strong> <FeedbackFormatter rawResponse={query.answer_text || 'Awaiting response...'} /></Text>

                                    {role === 'teacher' && (
                                        <FormControl mt={4}>
                                            <FormLabel>Answer:</FormLabel>
                                            <Textarea
                                                placeholder="Enter your answer..."
                                                value={answerTexts[query.query_id] || ''}
                                                onChange={(e) => handleAnswerChange(query.query_id, e.target.value)}
                                                bg="gray.50"
                                                _hover={{ bg: 'gray.100' }}
                                                _focus={{ bg: 'white', borderColor: 'blue.500' }}
                                                rows={3}
                                            />
                                            <Button
                                                colorScheme="blue"
                                                mt={3}
                                                onClick={() => handleAnswerSubmit(query.query_id)}
                                                isLoading={loading}
                                                borderRadius="full"
                                            >
                                                Submit Answer
                                            </Button>
                                        </FormControl>
                                    )}
                                </Box>
                            );
                        })
                    )}
                </VStack>
            </Box>

            {loading && (
                <Box
                    position="fixed"
                    top="0"
                    left="0"
                    width="100%"
                    height="100%"
                    backgroundColor="rgba(0, 0, 0, 0.5)"
                    zIndex="9999"
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    color="white"
                >
                    <Spinner size="xl" />
                    <Text ml={4}>LLM is fetching your answer...</Text>
                </Box>
            )}
        </Box>
    );
};

export default Queries;
