import React, { useEffect, useState } from 'react';
import axios from 'axios';
import {
  Box, VStack, Heading, Text, Divider, Button, Select, Textarea, FormControl, FormLabel, Spinner, Alert, AlertIcon, HStack, Stack, Icon
} from '@chakra-ui/react';
import { FiSend } from 'react-icons/fi'; // For the send button icon

const Feedback = () => {
  const [students, setStudents] = useState([]);
  const [selectedStudent, setSelectedStudent] = useState('');
  const [feedbackText, setFeedbackText] = useState('');
  const [feedbacks, setFeedbacks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [role, setRole] = useState('');

  useEffect(() => {
    async function fetchData() {
      try {
        const sessionResponse = await axios.get('/api/session');
        setRole(sessionResponse.data.role);

        const studentsResponse = await axios.get('/api/feedback');
        setStudents(studentsResponse.data.students);

        const feedbacksResponse = await axios.get('/api/feedback');
        setFeedbacks(feedbacksResponse.data.feedbacks);
      } catch (err) {
        console.error(err);
        setError('Failed to load data.');
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  const handleFeedbackSubmit = async (e) => {
    e.preventDefault();
    if (!selectedStudent || !feedbackText) {
      setError('Please select a student and provide feedback.');
      return;
    }

    setLoading(true);
    try {
      await axios.post('/api/feedback', {
        student: selectedStudent,
        feedback: feedbackText,
      });

      setSuccess('Feedback submitted successfully!');
      setError('');
      setFeedbackText('');
      setSelectedStudent('');

      const feedbackResponse = await axios.get('/api/feedback');
      setFeedbacks(feedbackResponse.data.feedbacks);
    } catch (err) {
      setError('Failed to submit feedback.');
      setSuccess('');
    } finally {
      setLoading(false);
    }
  };

  const sortedFeedbacks = [...feedbacks].sort(
    (a, b) => new Date(b.submission_date) - new Date(a.submission_date)
  );

  return (
    <Box
      p={8}
      maxW="7xl"
      mx="auto"
      bg="white"
      borderRadius="lg"
      boxShadow="lg"
      bgGradient="linear(to-r, gray.100, gray.200)"
      transition="all 0.2s ease-in-out"
    >
      <Heading as="h2" size="xl" textAlign="center" mb={6} color="blue.500">
        Feedback Management
      </Heading>

      {error && (
        <Alert status="error" mb={4} borderRadius="md">
          <AlertIcon />
          {error}
        </Alert>
      )}
      {success && (
        <Alert status="success" mb={4} borderRadius="md">
          <AlertIcon />
          {success}
        </Alert>
      )}

      {role === 'teacher' && (
        <Box
          as="form"
          onSubmit={handleFeedbackSubmit}
          p={6}
          borderWidth={1}
          borderRadius="md"
          bg="white"
          boxShadow="lg"
        >
          <Stack spacing={6}>
            <FormControl isRequired>
              <FormLabel>Select Student</FormLabel>
              <Select
                placeholder="--Select a Student--"
                value={selectedStudent}
                onChange={(e) => setSelectedStudent(e.target.value)}
                bg="white"
                borderColor="blue.500"
                _hover={{ borderColor: 'blue.400' }}
              >
                {students.map((student) => (
                  <option key={student.username} value={student.username}>
                    {student.username}
                  </option>
                ))}
              </Select>
            </FormControl>

            <FormControl isRequired>
              <FormLabel>Provide Feedback</FormLabel>
              <Textarea
                value={feedbackText}
                onChange={(e) => setFeedbackText(e.target.value)}
                rows={4}
                placeholder="Enter your feedback here..."
                borderColor="blue.500"
                _hover={{ borderColor: 'blue.400' }}
                focusBorderColor="blue.400"
              />
            </FormControl>

            <Button
              type="submit"
              colorScheme="blue"
              size="lg"
              rightIcon={<Icon as={FiSend} />}
              isLoading={loading}
              _hover={{ transform: 'translateY(-2px)', boxShadow: 'lg' }}
            >
              Submit Feedback
            </Button>
          </Stack>
        </Box>
      )}

      <Heading as="h3" size="lg" mt={10} mb={6} textAlign="center" color="blue.600">
        Previous Feedbacks
      </Heading>

      {loading ? (
        <Spinner size="xl" />
      ) : (
        <VStack spacing={6} align="stretch">
          {sortedFeedbacks.length > 0 ? (
            sortedFeedbacks.map((item) => (
              <Box
                key={item.id}
                p={6}
                borderWidth={1}
                borderRadius="lg"
                shadow="md"
                bg="gray.50"
                _hover={{ shadow: 'lg', bg: 'gray.100' }}
              >
                <HStack justify="space-between">
                  <Text fontWeight="bold" color="blue.600">
                    Feedback from {item.teacher_name || 'Unknown'}
                  </Text>
                  <Text fontSize="sm" color="gray.500">
                    {new Date(item.submission_date).toLocaleDateString()}
                  </Text>
                </HStack>
                <Divider my={4} />
                <Text fontSize="md" color="gray.700">
                  {item.feedback_text}
                </Text>
                <Text fontSize="sm" color="gray.600" mt={2}>
                  <strong>For Student:</strong> {item.student_name}
                </Text>
              </Box>
            ))
          ) : (
            <Text>No feedback submitted yet.</Text>
          )}
        </VStack>
      )}
    </Box>
  );
};

export default Feedback;
