import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { Box, Flex } from '@chakra-ui/react';
import Header from './Header';
import Sidebar from './Sidebar';
import Login from './Login';
import Dashboard from './Dashboard';
import Register from './Register';
import Assignments from './Assignments';
import axios from 'axios';
import Feedback from './Feedback';
import CourseMaterials from './CourseMaterials';
import Queries from './Queries';

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [username, setUsername] = useState('');
  const [role, setRole] = useState('');
  const [collapsed, setCollapsed] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    const storedRole = localStorage.getItem('role');
  
    if (storedUsername && storedRole) {
      setIsLoggedIn(true);
      setUsername(storedUsername);
      setRole(storedRole);
      setLoading(false);
    } else {
      axios.get('/api/session')
        .then(response => {
          if (response.data.logged_in) {
            setIsLoggedIn(true);
            setUsername(response.data.username);
            setRole(response.data.role);
            console.log('Role from session:', response.data.role);
          } else {
            console.log('User not logged in, redirecting to login.');
          }
        })
        .catch(error => {
          // Check if the error is a 401 (Unauthorized) and suppress logging if so
          if (error.response && error.response.status === 401) {
            console.log('No active session. User is not logged in.');
          } else {
            console.error('Session check failed:', error.response ? error.response.data : error.message);
          }
        })
        .finally(() => {
          setLoading(false);
        });
    }
  }, []);
  


  const handleLogin = (username, role) => {
    localStorage.setItem('username', username);
    localStorage.setItem('role', role);
    setIsLoggedIn(true);
    setUsername(username);
    setRole(role);
    console.log('Role after login:', role);
  };

  const handleLogout = () => {
    localStorage.removeItem('username');
    localStorage.removeItem('role');
    setIsLoggedIn(false);
    setUsername('');
    setRole('');
  };

  if (loading) {
    return <div>Loading...</div>; 
  }

  return (
    <Router>
      <Flex>
        {(
          <Sidebar
            isLoggedIn={isLoggedIn}
            onLogout={handleLogout}
            collapsed={collapsed}
            setCollapsed={setCollapsed}
          />
        )}

        <Box ml={isLoggedIn ? "80px" : "0"} p={0} flex="1">
          <Header isLoggedIn={isLoggedIn} username={username} />
          <Routes>
            <Route path="/login" element={isLoggedIn ? <Navigate to="/dashboard" /> : <Login onLogin={handleLogin} />} />
            <Route path="/register" element={isLoggedIn ? <Navigate to="/dashboard" /> : <Register />} />
            <Route path="/dashboard" element={isLoggedIn ? <Dashboard /> : <Navigate to="/login" />} />
            <Route path="/assignments" element={isLoggedIn ? <Assignments /> : <Navigate to="/login" />} />
            <Route path="/feedback" element={isLoggedIn ? <Feedback /> : <Navigate to="/login" />} />
            <Route path="/materials" element={isLoggedIn ? <CourseMaterials /> : <Navigate to="/login" />} />
            <Route path="/queries" element={isLoggedIn ? <Queries /> : <Navigate to="/login" />} />
            <Route path="*" element={<Navigate to={isLoggedIn ? "/dashboard" : "/login"} />} />
          </Routes>
        </Box>
      </Flex>
    </Router>
  );
}

export default App;
