import React from 'react';
import { Box, Flex, Text, Heading, Button } from '@chakra-ui/react';

function Dashboard() {
  return (
    <Flex
      direction="column"
      align="center"
      justify="center"
      minHeight="82vh"
      bgGradient="linear(to-r, teal.400, blue.500)"
      p={6}
    >
      <Box
        bg="white"
        p={8}
        color="gray.700"
        borderRadius="lg"
        shadow="xl"
        maxWidth="600px"
        textAlign="center"
        transition="all 0.3s ease"
        _hover={{ transform: 'scale(1.05)', shadow: '2xl' }}
      >
        <Heading as="h1" fontSize="3xl" color="blue.500" mb={4}>
          Welcome to Your Dashboard
        </Heading>
        <Text fontSize="lg" mb={6}>
          Here you can manage all your course materials, view analytics, and more.
        </Text>
        <Button
          size="lg"
          colorScheme="teal"
          variant="solid"
          _hover={{ bg: 'teal.600' }}
        >
          Get Started
        </Button>
      </Box>
    </Flex>
  );
}

export default Dashboard;
