import React from 'react';
import DOMPurify from 'dompurify';

const FeedbackFormatter = ({ rawResponse }) => {
    const formatResponse = (response) => {
        // Replace line breaks with <p> tags for paragraphs
        let formatted = response.split('\n').map(line => line.trim()).filter(line => line).join('</p><p>');
        formatted = `<p>${formatted}</p>`;

        // Replace bullet points and numbered lists
        formatted = formatted
            .replace(/\*\s\*\s/g, '<h3>') // Convert "**" to <h3>
            .replace(/\*\s/g, '<li>') // Convert "*" to <li>
            .replace(/<\/li><li>/g, '</li><li>')
            .replace(/<h3>(.*?)<\/h3>/g, '<h3>$1</h3>')
            .replace(/<li>(.*?)<\/li>/g, '<ul><li>$1</li></ul>')
            .replace(/<ul><li>(.*?)<\/li><\/ul>/g, '$1'); // Remove empty <ul>

        // Replace numbered items
        formatted = formatted
            .replace(/(\d+)\.\s/g, '<li>$1. ');

        return DOMPurify.sanitize(formatted); // Sanitize the HTML
    };

    return (
        <div dangerouslySetInnerHTML={{ __html: formatResponse(rawResponse) }} />
    );
};

export default FeedbackFormatter;
